import React from 'react';
import './App.css';

class App extends React.Component<{},{name:string,arra:{name:string}[],sugg:{name:String}[]}>{
  constructor(props:any)
  {
    super(props);
    var red: string ="";
    var arr: {name: string}[]=[{name:"abandon"},{name:"ability"},{name:"able"},{name:"abortion"},{name:"about"},{name:"accompany"},{name:"achieve"},{name:"acknowledge"},{name:"acquire"},{name:"adapt"}
    ,{name:"additional"},{name:"advance"},{name:"afford"},{name:"African-American"},{name:"aggressive"},{name:"agricultural"},{name:"amazing"},{name:"blanket"},{name:"boat"},{name:"bomb"},{name:"border"},{name:"bread"},{name:"briefly"}
    ,{name:"cable"},{name:"camera"},{name:"candidate"},{name:"circumstance"},{name:"dead"},{name:"demand"},{name:"difficulty"},{name:"dozen"},{name:"during"},{name:"east"},{name:"economics"},{name:"entire"},{name:"extend"},{name:"fix"},{name:"flag"}
    ,{name:"German"},{name:"glance"},{name:"greatest"},{name:"guide"},{name:"herself"},{name:"holiday"},{name:"house"},{name:"identification"},{name:"ideal"},{name:"imagination"},{name:"implement"}] ;
    this.state= {name:red,arra:arr,sugg:arr};
    this.ramj=this.ramj.bind(this);
  }
  ramj(e:{target:{value:string}}) :void
  {
    var z:{name:string}[]=[];
    var l:string=e.target.value;
    if(l === "")
     {
        this.setState({sugg:this.state.arra});
     }
     else{
         var tx:{name:string}[]=this.state.arra;
         for(let i=0;i<this.state.arra.length;i++)
         {
           if(tx[i].name.substring(0,l.length)=== l)
           {
             z.push(tx[i]);
           }
         }
         this.setState({sugg:z});
     }
     this.setState({name:l});
  }
 render()
 { 
  var s:string="http://www.google.com"
  return (
     <div>
       <input type="text" value={this.state.name} onChange={this.ramj} ></input><br />
       <ul style={{overflow: "auto",height:"500px",width:"150px"}}>
         {this.state.sugg.map(suggestion=>{
           return <p><a href={s} style={{textDecoration:"none"}}>{suggestion.name}</a> </p>
         })}
       </ul>
     </div>
   );
 } 
}

export default App;
